body {
  background-color: #fff;
  margin: 0;
  height: auto;
  perspective: 125vmin;
  -webkit-filter: drop-shadow(-1vmin 1vmin 1vmin #000000d3);
}

.container {
  width: 100%;
  max-width: 600px;
  background: #3d3d3d;
  padding: 40px;
  color: #fff;
  text-align: center;
  margin: 35px auto;
  display: block;
  box-shadow: 3px 3px 15px #ccc;
  border-radius: 20px;
}
.container h1 {
  font-size: 7em;
  font-weight: background-color;
}
.container p {
  font-size: 2em;
  margin-top: 2em;
  line-height: normal;
}
.container img {
  box-shadow: 3px 3px 10px #2e2e2e;
  border-radius: 20px;
}